@import url(https://fonts.googleapis.com/css?family=Bangers);
@import url(https://fonts.googleapis.com/css?family=Nixie+One);
@import url(https://fonts.googleapis.com/css?family=Bungee);
@import url(https://fonts.googleapis.com/css?family=Bungee);
html, body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image: url("http://i.imgur.com/Cp8TLZH.png"); */
  background-color: black;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
    /* padding-top: 50px; */
    /* background: #ECEFF1; */
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
nav {
  width: 100%;
  font-size: 3rem;
  color: #000;
  height: auto;
  background-color: yellow;
  align-content: center;
}

ul {
  display: flex;
  align-items: center;
  margin-bottom: 1%;
  margin-top: 1%;
  padding-left: 0;
}

nav li {
  display: inline-block;
  margin-left: 6%;
  /* margin-right: 1%; */
}

ul h1, ul h4 {
  margin-bottom: 0;
}

h1 {
  font-family: "Bangers";
  font-size: 2em;
  color: red;
  text-shadow: 2px 2px 5px black;
}

.message {
  margin-left: 13%;
  font-family: "Nixie One";
}

.score {
    text-align: center;
    font-family: "Bungee";
    font-size: 1.5em;
    color: white;
}
.row {
  margin-top: 3%;
}

.card {
    border-radius: 2px;
    width: 175px;
    height: 175px;
    margin: 1rem;
    position: relative;
    transition: all .2s ease-in-out;
    background: black;
    /* overflow: hidden; */
  }
  
  .card > .img-container {
    height: 100%;
    align-self: center;
}

.card > .img-container > img {
    height: 100%;
    align-content: center;
  }

.card:hover {
    border: 4px rgb(255, 255, 255) solid;
}
.pokemonRow {
  justify-content: center;
  margin-top: 0px;
}

.clickMessage {
  justify-content: center;
  margin-top: 0px;
  padding-top: 1%;
  padding-bottom: 1%;
}

.clickMessage p {
  margin-bottom: 0px;
  color: white;
  font-family: "Bungee";
  font-size: 1.5em;
}
